import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

const store = new Vuex.Store({
    state: {
        topics: [
            // tab
            { name: "all", desc: "" },
            { name: "good", desc: "" },
            { name: "share", desc: "" },
            { name: "ask", desc: "" },
            { name: "job", desc: "" },
            { name: "dev", desc: "" }
        ],
        isShowMoreMenus: false, //actionsheet
        // moreMenus: //,,,,
        isLogined: false, //
        unLoginedMenus: {
            login: ''
        },
        loginedMenus: {
            center: '',
            create: '',
            msg: '',
            logout: ''
        },
        loginname: '', //
        accesstoken: '', //accesstoken
        authorId: '', //ID
    },
    getters: {
        moreMenus(state) {
            if (state.isLogined) {
                return state.loginedMenus
            }
            return state.unLoginedMenus
        }
    },
    mutations: {
        //actionsheet
        mutationIsShowMoreMenus: function(state, payload) {
            state.isShowMoreMenus = payload;
        },
        //
        mutationIsLogined: function(state) {
            state.isLogined = true
        },
        //
        mutationIsLogined: function(state) {
            state.isLogined = true;
        },
        //accesstoken
        mutationAccesstoken: function(state, payload) {
            state.accesstoken = payload;
        },
        //id
        mutationAuthorId: function(state, payload) {
            state.authorId = payload;
        },
        //
        mutationLogout: function(state) {
            state.isLogined = false;
            state.loginname = "";
            state.accesstoken = '';
            state.authorId = '';
        }

    },
    actions: {}
});
export default store;


